import React, { Component } from "react";
import './NavBar.css';
import '../../../static/Grid.css';
import {Switch, Route, Link, useLocation} from "react-router-dom";
import ls from 'local-storage';
import {MyContext} from '../../context/context';
import Login from '../login/login';



function DashboardBtnGroup() {
   return(    <div className='nav-links-box'>
         <Link
            to={{pathname: "/Upload"}}
            className="nav-link"
          >
          Upload
          </Link>

          <Link
            to={{pathname:'/home'}}
            className="nav-link"
          >
          Home
          </Link>

        
        
        </div>)
}


function LoginSignUpButtons(){
  return(    <div className='nav-links-box'>
       <Link
            to={{pathname: "/Login"}}
            className="nav-link"
          >
          Login
          </Link>

          <Link
            to={{pathname:'/Signup'}}
            className="nav-link"
          >
          signUp
          </Link>
        </div>)
}

function DashBoardLogoutButtons(){
  return(    <div className='nav-links-box'>
         
       <Link
            to={{pathname: "/Logout"}}
            className="nav-link"
          >
          Logout
          </Link>

          <Link
            to={{pathname:'/Dashboard'}}
            className="nav-link"
          >
          Dashboard
          </Link>

        
        
        </div>)
}

function NavBarBtnGroup(){
  let location = useLocation();
  console.log(location);
  if(location.pathname==='/Dashboard') {
    return(<DashboardBtnGroup/>)
  }
  else{
    return(<MyContext.Consumer>
          {(context) => (context.state.isLoggedIn ? <DashBoardLogoutButtons/> : <LoginSignUpButtons/>)}
        </MyContext.Consumer>)
  }
}

class NavBar extends Component{


componentDidUpdate() {

}


render() {


	return(

<div className='Nav row'> 
         
         <div className='col-6'>
         <div className='logo'>
             IntWiff
	    </div>
	    </div>
	    <div className='col-6'>
       <NavBarBtnGroup/>
       
        </div>
    
</div>

		)
}
}


export default NavBar;
